import React, { Component } from 'react';
import Footer from '../../components/Footer'
import { withRouter } from 'react-router-dom'
import { service } from '../../actions/service'
import { message } from 'antd'
import MainHeader from '../../components/MainHeader';
const loading = "data:image/gif;base64,";
class VerifyCompany extends Component {
    constructor(props) {
        super(props);

        this.state = {
            idCompany: this.props.match.params.id,
            hash: this.props.match.params.hash,
            isLoading: true,
            success: false,
        };
    }
    componentDidMount() {
        if (localStorage.getItem('res')) {
            this.props.history.push('/')
        }
        else if (localStorage.getItem('company')) {
            this.props.history.push('/company/profile-admin')
        }
        const postParam = {
            id: this.state.idCompany,
            hash: this.state.hash,
        }
        // console.log(postParam);
        service.verifyCompany(postParam)
            .then(res => {
                if (res.status === 200) {
                    this.setState({
                        isLoading: false,
                        success: true,
                    })
                    message.success('Xác nhận thành công')
                }
            })
            .catch(err => {
                // console.log(err)
                this.setState({
                    isLoading: false,
                    success: false,
                })
                message.error('Lỗi, mời bạn vui lòng thử lại')
            })
    }
    onLogin = (e) => {
        e.preventDefault();
        this.props.history.push('/company/login')
    }
    render() {

        return (
            <div>
                <MainHeader />
                <div className="container">
                    <div className="verify" style={{ height: '50px', paddingTop: '10px', paddingLeft: '10px', marginTop: '70px', backgroundColor: 'lightblue' }}>
                        <h3 style={{ textTransform: 'Uppercase' }}>Xác thực tài khoản công ty</h3>
                    </div>
                    {!this.state.isLoading && this.state.success ?
                        <div className="jumbotron">
                            <h4>Xác thực thành công</h4>
                            <p style={{ fontSize: '16px' }}>Tài khoản đã được xác thực thành công, hãy đăng nhập để tiếp tục sử dụng dịch vụ</p>
                            <button style={{ width: '100px' }} type="button" class="btn btn-block btn-primary" onClick={this.onLogin}>Đăng nhập</button>
                        </div> :
                        <div>
                            {!this.state.isLoading && !this.state.success ?
                                <div className="jumbotron">
                                    <h4>Lỗi xác thực</h4>
                                    <p style={{ fontSize: '16px' }}>Tài khoản không tồn tại hoặc đã được xác thực, vui lòng kiểm tra lại</p>
                                </div> :
                                <div className="jumbotron">
                                    <h4>Vui lòng chờ trong giây lát ...</h4>
                                    <img src={loading} alt="" />
                                </div>
                            }
                        </div>
                    }
                </div>
                <Footer />
            </div>
        );
    }
}

export default withRouter(VerifyCompany);